import '../../../libs/slick/slick.min';

$(document).ready(function () {

  $('.detail-slider').slick({
    dots: false,
    arrows: false,
    infinite: true,
    vertical: true,
    speed: 600,
    autoplay: false,
    autoplaySpeed: 2000,
    slidesToShow: 1,
    slidesToScroll: 1,
  });

  $('.circle_prev').on('click', function () {
    $('.detail-slider').slick('slickPrev')
  });

  $('.circle_next').on('click', function () {
    $('.detail-slider').slick('slickNext')
  });

  $('.similar-slider').slick({
    dots: false,
    arrows: false,
    infinite: true,
    speed: 600,
    autoplay: false,
    autoplaySpeed: 2000,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1200,
        settings: {
          centerMode: true,
          centerPadding: '12%',
          slidesToShow: 3,
          slidesToScroll: 1,
        }
      },
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
        }
      },
      {
        breakpoint: 540,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  });
});
